// polyfill for atob & btoa

import {
  Base64,
} from 'js-base64'
const { btoa, atob } = Base64
import global from 'global'
import { Buffer } from 'buffer'

function polyfill () {
  if (!global.atob || !global.btoa) {
    global.btoa = btoa
    global.atob = atob
  }
  if (!global.Buffer) {
    global.Buffer = Buffer
  }
}

export default polyfill
